.checkoutItem {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;
  .imageContainer {
    width: 23%;
    padding-right: 15px;
    .imgStyle {
      background-position: center;
      background-size: cover;
      width: 100%;
      display: flex;
      height: 70px; } }
  .name,
  .quantity,
  .price {
    width: 23%; }
  .quantity {
    display: flex;
    .arrow {
      cursor: pointer; }
    .value {
      margin: 0 10px; } }
  .removeButton {
    padding-left: 12px;
    cursor: pointer; } }
